* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.app {
  background-image: url('./assets/default.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top fixed;
  transition: 0.5s ease;
}
.app.clouds {
  background-image: url('./assets/clouds.jpg');
}
.app.rain,
.app.drizzle {
  background-image: url('./assets/rain.jpg');
}
.app.clear {
  background-image: url('./assets/sun.jpg');
}
.app.thunderstorm {
  background-image: url('./assets/thunderstorm.jpg');
}
.app.snow {
  background-image: url('./assets/snow.jpg');
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.55)
  );
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}
.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  /* appearance: none; */
  /* background: none; */
  /* outline: none; */
  border: none;

  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 50px;
  margin-bottom: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #202020;
  font-size: 30px;
  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.85);
  /* border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  margin-bottom: 0px; */
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  padding: 10px;
}

.weather-box,
.error-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  /* text-align: center; */
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.weather-box .weather img {
  vertical-align: middle;
  /* margin-bottom: 10px;
  margin-left: 10px; */
  margin: 0px 0px 10px 10px;
}

.error,
.message {
  position: relative;
  display: inline-block;

  text-align: center;
  color: #fff;
  font-size: 48px;
  text-shadow: 3px 3px rgba(0, 0, 0, 0.5);
  text-transform: capitalize;

  margin: 40px;
  padding: 15px;

  /* border: 1px solid black; */
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.message {
  font-size: 40px;

  text-transform: none;
}
